import {
	getLogin,
} from '../../../service/api/login.js' //登陆api
var tha;
export default {
	onLoad(){
		tha = this;
	},
	data() {
		return {
			phoneno:'',
			password:''
		};
	},
	created() {
		this.isLogin();
	},
	methods: {
		isLogin(){
			//判断缓存中是否登陆过，直接登陆
			var _this=this
			try {
				const value = uni.getStorageSync('setUserData');
				if (value) {
					//有登陆信息
					console.log("已登录用户：",value);
					_this.$store.dispatch("setUserData",value); //存入状态
					uni.reLaunch({
						url: '../../../pages/index',
					});
				}
			} catch (e) {
				// error
			}
		},
	    bindLogin() {
			var _this=this
			// if (this.phoneno.length != 11) {
			//      uni.showToast({
			//         icon: 'none',
			// 		position: 'bottom',
			//         title: '手机号不正确'
			//     });
			//     return;
			// }
			if (this.phoneno.length == "") {
			     uni.showToast({
			        icon: 'none',
					position: 'bottom',
			        title: '用户名不能为空'
			    });
			    return;
			}
	        if (this.password.length < 5) {
	            uni.showToast({
	                icon: 'none',
					position: 'bottom',
	                title: '密码不正确'
	            });
	            return;
	        }
			uni.showLoading({
				title: '登陆中'
			});
			getLogin()
			.then(res => {
				//console.log(res)
				//简单验证下登陆（不安全）
				if(_this.phoneno==res.data.username && _this.password==res.data.password){
					let userdata={
						"username":res.data.username,
						"nickname":res.data.nickname,
						"accesstoken":res.data.accesstoken,
					} //保存用户信息和accesstoken
					_this.$store.dispatch("setUserData",userdata); //存入状态
					try {
						uni.setStorageSync('setUserData', userdata); //存入缓存
					} catch (e) {
						// error
					}
					uni.showToast({
						icon: 'success',
						position: 'bottom',
						title: '登陆成功'
					});
					uni.reLaunch({
						url: '../../../pages/index',
					});
				}else{
					_this.password=""
					uni.showToast({
						icon: 'error',
						position: 'bottom',
						title: '账号或密码错误，账号admin密码admin'
					});
				}
				uni.hideLoading();
			}).catch(err => {
				uni.hideLoading();
			})
			
			
			
			
			
	    }
	}
}
